@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz@6..96&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,500;1,700&display=swap");

:root {
  --primary-color: #343a40;
  --secondary-color: #343a40;
}

* {
  padding: 0;
  margin: 0;
  font-family: Poppins, serif;
  text-decoration: none;
}

body {
  background-color: #f8f9fa;
}

html {
  font-size: 75.5%;
}

.navbar {
  width: 100%;
  background-color: #007bff;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem 2%;
  position: fixed;
}

.navbar a {
  font-size: 2.5rem;
  color: #fff;
  font-weight: 700;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.navbar a span {
  font-family: Poppins;
  font-weight: 600;
  font-size: 2.3rem;
  color: rgb(255, 185, 56);
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.hero {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.35);
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(./bg1.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero .hero-content {
  text-align: center;
  margin: 0 auto;
  flex: 0.5 0.5 1rem;
}

.hero h1 {
  font-size: 3rem;
  margin-top: 20px;
  margin-bottom: 15px;
}

.search-box {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
  background-color: #fff;
  width: 100%;
}

.search-box input {
  width: 100%;
  font-size: 15px;
  background: none;
  outline: none;
  border: none;
}

.search-box i {
  padding: 10px 15px;
  color: #343a40;
  font-size: 18px;
}

.container {
  max-width: 900px;
  margin: 25px auto;
  display: grid;
  grid-template-columns: repeat(4, minmax(200px, 0.5fr));
  grid-gap: 20px;
  box-sizing: border-box;
  background-color: #fff;
  padding: 20px;
}

.card {
  background-color: #fff;
  border-radius: 1rem;
  padding: 0.5rem;
  margin-bottom: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.35);
  transition: 0.5s;
  text-align: center;
  cursor: pointer;
}

.card:hover {
  transform: scale(0.9);
  background-color: rgba(255, 255, 255, 0.8);
}

.card img {
  width: 100%;
  height: 200px;
  border-radius: 1rem 1rem 0 0;
}

.card .bottom {
  min-height: 90px;
}

.card .bottom .title {
  font-size: 14px;
  margin-bottom: 32px;
}

.card .bottom .amount {
  bottom: 10px;
  left: 10px;
  right: 10px;
  background-color: #fff;
  color: #343a40;
  font-size: 13px;
  font-weight: bold;
  padding: 0.2rem;
}

.overlay {
  min-height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1));
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay-inner {
  background: #fff;
  width: 700px;
  height: 550px;
  padding: 2rem;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  font-size: 1.3rem;
}

.overlay-inner .close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  outline: none;
  border: none;
  font-size: 17px;
  background-color: transparent;
  cursor: pointer;
  background-color: #343a40;
  color: #fff;
  padding: 0 0.6rem;
  border-radius: 50%;
}

.overlay-inner .inner-box {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.overlay-inner .inner-box img {
  margin-left: 20px;
  width: 150px;
  height: 200px;
}

.overlay-inner .inner-box h3 {
  margin-top: 10px;
  color: green;
}

.overlay-inner .inner-box h4 {
  color: blue;
}

.overlay-inner .inner-box button {
  outline: none;
  border: none;
  width: 100px;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  font-weight: bold;
  padding: 0.3rem 0.5rem;
  cursor: pointer;
  display: inline-block;
}

.overlay-inner .inner-box button:hover {
  background-color: #036cdd;
}

.overlay-inner .description {
  margin-top: 2rem;
  text-align: justify;
  font-size: 1rem;
  font-weight: 400;
}

@media (max-width: 768px) {
  .hero .hero-content h1 {
    font-size: 2rem;
  }

  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .overlay-inner .inner-box h1 {
    font-size: 2rem;
  }

  .overlay-inner .inner-box h3 {
    margin-top: 10px;
    color: green;
    font-size: 1.4rem;
  }
}
